section#about{
  width: get-vh(1920px * 1.5);
  height: 100vh;

  position: relative;

  background: $main-black;
}

.about--header{
  width: get-vh(1064.46px);
  height: get-vh(111.65px);
  top: get-vh(369px);
  left: get-vh(80px);

  position: absolute;

  background: url('/images/about_header.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.about--p-1{
  width: get-vh(657px);
  top: get-vh(509px);
  left: get-vh(488px);

  position: absolute;

  font-family: 'Comfortaa';
  font-weight: 300;
  font-size: get-vh(24px);
  line-height: get-vh(28px);
  text-align: right;
  color: rgba(255,255,255, 0.8);
}

.about--p-1
span{
  font-family: 'Roboto';
  font-weight: 500;
  font-size: get-vh(18px);
  line-height: get-vh(18px);
  color: rgba(255,255,255, 0.4);
}

.about--p-2{
  width: get-vh(690px);
  top: get-vh(675px);
  left: get-vh(244px);

  position: absolute;

  font-family: 'Montserrat';
  font-weight: 600;
  font-size: get-vh(19px);
  line-height: get-vh(18px);
  color: rgba(255,255,255, 0.4);
}
