section#service{
  width: get-vh(1920px);
  height: 100vh;

  position: relative;
  background: url('/images/services_static.svg');
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0 -1px;
}


ul{
  list-style: none;
}

.service--changeable{

}

/*text-blocks*/

.service--text-service,
.service--text-team{
  position: absolute;
  opacity: 0;
  animation: fadeIn 0.25s 0.25s forwards;
}

.service--text-1.service--text-service{
  width: get-vh(380px);

  left: get-vh(815px);
  top: get-vh(290px);
}

.service--text-2.service--text-service{
  width: get-vh(416px);

  left: get-vh(254px);
  top: get-vh(702px);
}

.service--text-3.service--text-service{
  width: get-vh(399px);

  left: get-vh(1314px);
  top: get-vh(702px);
}

.service--text-1.service--text-team{
  width: get-vh(296px);

  left: get-vh(815px);
  top: get-vh(332px);
}

.service--text-2.service--text-team{
  width: get-vh(203px);

  left: get-vh(336px);
  top: get-vh(742px);
}

.service--text-3.service--text-team{
  width: get-vh(306px);

  left: get-vh(1336px);
  top: get-vh(742px);
}

/*headers*/

.service--text-service
h2{
  margin-bottom: get-vh(23px);

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(35px);
}

.service--text-team
h2{
  margin-bottom: get-vh(27px);

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(35px);
  text-align: center;
}

.service--text-1
h2{
  color: #E99B5E;
}

.service--text-2
h2{
  color: #E99B5E;
}

.service--text-2.service--text-service
h2{
  padding-left: get-vh(82px);
}

.service--text-3
h2{
  color: #E99B5E;
}

.service--text-3.service--text-service
h2{
  padding-left: get-vh(24px);
}

/*lists*/

.service--text-service
ul{
  display: grid;

  grid-auto-flow: row;
  grid-row-gap: get-vh(16px);
}

.service--text-service
li{
  font-family: 'Roboto-Medium';
  font-size: get-vh(20px);
  line-height: get-vh(22px);
  color: rgba(255, 255, 255, 0.4);
  text-align: left;
}

.service--text-team
li{
  font-family: 'Roboto-Medium';
  font-size: get-vh(20px);
  line-height: get-vh(27px);
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
}

/*animations*/

@keyframes fadeIn {
 from { opacity: 0; }
 to { opacity: 1; }
}

@keyframes fadeOut {
 from { opacity: 1; }
 to { opacity: 0; }
}

/*state headers*/

.controls-container{
  width: get-vh(460px);
  height: get-vh(42px);
  top: get-vh(213px);
  left: 0; right: 0;
  margin: auto;

  position: absolute;
  display: grid;

  grid-auto-flow: column;
  grid-column-gap: get-vh(20px);
  grid-template-columns:
  1fr 60px 1fr;

  align-items: center;
}

.controls-container h2:first-child{
  text-align: right;
}

.controls-container h2{
  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(20px);
  color: rgba(255, 255, 255, 0.4);
  transition: 0.3s
}

.controls-container h2.selected{
  font-size: get-vh(35px);
  color: rgb(233, 155, 94);
}

/*switch*/

.switch{
  width: get-vh(48px);
  height: get-vh(24px);

  position: relative;
  justify-self: center;

  border: get-vh(2px) solid rgba(255, 255, 255, 0.4);
  border-radius: get-vh(38px);
}

.switch input{
  width: 0;
  height: 0;
  opacity: 0;
}

.slider{
  position: absolute;
  top:0; bottom:0; margin: auto;
  left: get-vh(-6px);
  width: get-vh(30px);
  height: get-vh(30px);
  background-color: #E78F62;
  border-radius: 50%;
  transition: 0.3s;
}

.switch input:checked + .slider{
  transform: translateX(get-vh(26px));
}
