@import url('https://fonts.googleapis.com/css?family=Comfortaa:300,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto:500,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Montserrat:600&display=swap');

@import './scss/variables.scss';
@import './scss/functions.scss';
@import './scss/about.scss';
@import './scss/header.scss';
@import './scss/service.scss';
@import './scss/cases.scss';
@import './scss/lightbox.scss';

*{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body{
  margin: 0;
  scroll-behavior: smooth;
}

p, h1, h2, li, ul, span{
  margin: 0;
  padding: 0;
  hyphens: auto;
}

a{
  text-decoration: none;
  color: inherit;
}

.App{
  width: 100vw;
  height: 100vh;
}

main{
  width: max-content;
  height: 100vh;
  overflow-y: hidden;

  display: flex;
}
