header{
  width: get-vh(940.5px);
  height: get-vh(81.5px);
  top: 0;
  left: 0;

  position: fixed;
  box-sizing: content-box;
  display: grid;
  align-items: center;
  grid-auto-flow: column;
  grid-column-gap: get-vh(15px);

  padding-top: get-vh(10px);
  padding-left: get-vh(20px);

  z-index: 1;
}

.logo{
  width: get-vh(120px);
  height: get-vh(68px);

  display: block;
  background: url('/images/logo.svg');
  background-size: cover;
}

.description{
  width: get-vh(318px);
  height: auto;

  display: inline-block;

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(19px);
  line-height: get-vh(18px);
  letter-spacing: get-vh(-0.5px);
  color: rgba(255,255,255, 1);
}

nav{
  position: relative;
}

nav::before{
  width: get-vh(2.32px);
  height: get-vh(46.39px);

  content: '';
  position: absolute;
  display: block;

  left: 0;
  top: 0; bottom: 0;
  margin: auto;

  border-radius: get-vh(5px);
  background-color: rgba(255,255,255, 0.5);
}

.nav-links{
  width: get-vh(396px);
  height: min-content;

  position: relative;
  display: flex;
  justify-content: space-between;

  list-style: none;
  padding-left: get-vh(24px);
}

.nav-item{
  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(21px);
  line-height: get-vh(21px);
  color: rgba(255,255,255, 0.5);
}

.social{
  margin-left: get-vh(35px);

  display: grid;
  grid-auto-flow: column;
  grid-column-gap: get-vh(10px);
}

.social-item{
  width: get-vh(20.53px);
  height: get-vh(20.53px);

  border-radius: 50%;
  background: rgba(255,255,255, 0.5);
}
