section#cases{
  width: get-vh(1920px);
  height: 100vh;
  background-color: $main-black;

  padding: 0 get-vh(139px);
  display: flex;

}

/*description*/

.cases--description{
  padding-left: get-vh(12px);
}

.cases--description p{
  font-family: 'Roboto';
  font-weight: 500;
  font-size: get-vh(20px);
  line-height: get-vh(27px);
}


/*categories*/

.cases--categories-container{
  width: get-vh(327px);
  height: 100%;
  flex-shrink: 0;

  padding: get-vh(196px) 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  color: $main-white;
}

.cases--categories-container h2{
  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(35px);
  line-height: get-vh(42px);
  color: #F7E246;
}

.cases--category h2{
  padding-left: get-vh(47px);
  margin-bottom: get-vh(10px);

  position: relative;

  color: rgba(247, 225, 69, 0.5);
}

.cases--category h2::before{
  width: get-vh(30px);
  height: get-vh(30px);
  left: get-vh(4px);
  bottom: get-vh(7px);

  position: absolute;
  display: block;
  content: '';
}

.cases--category-1 h2::before{
  background-image: url('/images/circle.svg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.cases--category-2 h2::before{
  background-image: url('/images/triangle.svg');
  background-size: 100%;
  background-repeat: no-repeat;
}

.cases--category-3 h2::before{
  background-image: url('/images/square.svg');
  background-size: 100%;
  background-repeat: no-repeat;
}

.cases--category ul{
  display: grid;
  grid-auto-flow: row;
  grid-row-gap: get-vh(7px);

  font-family: 'Roboto';
  font-weight: 500;
  font-size: get-vh(20px);
  line-height: get-vh(20px);
}

.cases--category ul li span{
  width: get-vh(34px);
  margin-right: get-vh(13px);

  display: inline-block;

  text-align: right;
  color: #FFFFFF;
}

/*clients*/

.cases--clients-container{
  flex-grow: 1;
  height: 100%;

  padding: get-vh(288px) 0  0 get-vh(184px);

  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(6, get-vh(42px));
  grid-row-gap: get-vh(40px);
}

.cases--client{
  width: max-content;

  display: grid;
  grid-auto-flow: column;
  align-items: center;

  grid-column-gap: get-vh(10px);
}

.cases--client h2{
  padding-right: get-vh(10px);

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(35px);
  line-height: get-vh(42px);
  color: $main-white;
}

.cases--client i{
  width: get-vh(25px);
  height: get-vh(25px);

  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
}
