/*lightbox window*/
#lightbox{
  width: 100vw;
  height: 100vh;
  top: 0; left: 0;

  padding: get-vh(170px) 0;

  position: fixed;
  display: flex;

  justify-content: center;

  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  overflow-y: scroll;
}

#posts{
  width: get-vh(1326px);
  height: max-content;

  border: $orange-yellow get-vh(3px) solid;
  border-radius: get-vh(50px);
  background-color: $main-black;
}

.lightbox-header{
  width: 100%;
  height: get-vh(251px);
  padding: get-vh(54px) get-vh(58px) get-vh(42px) get-vh(58px);

  background: url('/images/lightbox.svg');
  background-size: contain;
  background-position: bottom;
  background-repeat: no-repeat;
}

/*lightbox content header*/

.lightbox-header h2{
  font-family: 'Comfortaa';
  font-weight: lighter;

  font-size: get-vh(72px);
  line-height: get-vh(72px);
  color: $orange-yellow;
}

.lightbox-header h3{
  font-family: 'Roboto';
  font-weight: medium;

  font-size: get-vh(18px);
  line-height: get-vh(22px);
  color: rgba(255,255,255,0.6);
}

.lightbox-header--count{
  width: get-vh(590px);
  height: get-vh(56px);
  margin-top: 16px;

  display: flex;
  justify-content: space-between;
}

.lightbox-header--count li{
  font-family: 'Roboto';
  font-weight: bold;

  font-size: get-vh(15px);
  line-height: get-vh(15px);
  color: $main-white;

  display: flex;
  justify-content: space-between;
}

.lightbox-header--count span{
  margin-right: get-vh(11px);

  font-family: 'Comfortaa';
  font-weight: 400;

  font-size: get-vh(36px);
  line-height: get-vh(36px);
  color: $orange-yellow;
}

/*lightbox content post blocks*/

.lightbox-posts{
  padding: get-vh(68px) get-vh(54px);

  display: grid;
  grid-auto-flow: row;
  grid-row-gap: get-vh(68px);
}

.post{
  width: 100%;
  height: auto;

  padding-bottom: get-vh(39px);

  position: relative;
  display: grid;

  grid-auto-flow: column;
  grid-column-gap: get-vh(82px);
}

.post::after{
  width: get-vh(650px);
  height: get-vh(2px);
  right: 0; left:0;
  bottom: 0;

  margin: auto;

  position: absolute;
  display: block;

  background-color: #666668;
  content: '';
}

.post--img{
  width: get-vh(510px);
  height: 100%;
  flex-shrink: 0;

  padding-top: get-vh(76px);

  display: block;
  object-fit: contain;
}

.post--text{
  flex-grow: 1;
}

.post--date{
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: auto 1fr;
  grid-column-gap: 10px;

  justify-content: left;
  align-items: center;

  font-family: 'Comfortaa';
  font-weight: 400;
  font-size: get-vh(31px);
  line-height: get-vh(38.8px);

  color: #666668;
}

.post--date span{
  width: get-vh(84.07px);
  height: get-vh(38.8px);

  display: block;

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(27px);
  line-height: 4vh;
  text-align: center;

  border-radius: get-vh(25px);
  color: black;
  background-color: $orange-yellow;
}

.post--title{
  margin-top: get-vh(28px);
  padding-left: get-vh(15px);

  font-family: 'Comfortaa';
  font-weight: 400;
  font-size: get-vh(31px);
  line-height: get-vh(34px);

  color: #E68D21;
}

.post--content{
  margin-top: get-vh(6px);
  padding-left: get-vh(24px);

  font-family: 'Roboto';
  font-weight: medium;
  font-size: get-vh(25px);
  line-height: get-vh(26px);

  color: rgba(255,255,255,0.6);
}

.post--results{
  padding-left: get-vh(15px);
}

h3.post--results{
  margin-top: get-vh(18px);

  font-family: 'Comfortaa';
  font-weight: bold;
  font-size: get-vh(22px);
  line-height: get-vh(24px);

  color: #666668;
}

ul.post--results{
  margin-top: get-vh(19px);

  display: grid;
  grid-auto-flow: row;
  grid-row-gap: get-vh(25px);
}

ul.post--results
li{
  font-family: 'Roboto';
  font-weight: medium;
  font-size: get-vh(18px);
  line-height: get-vh(19px);

  color: rgba(255,255,255,0.6);
}

ul.post--results
li
span{
  padding-right: get-vh(20px);

  font-family: 'Comfortaa';
  font-weight: 400;
  font-size: get-vh(31px);
  line-height: get-vh(34px);

  color: #E68D21;
}
